import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  @Input() isLoggedIn:boolean;
  isOpen = false;
  constructor() { }

  ngOnInit() {
  }

  openLoginPopup() {
    this.isOpen = true;
  }


}



